@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App__App--1o-Fp h1{
    text-align: center;
    color: #fff;
    margin-bottom: 20px;


}
.App__App--1o-Fp{
    padding: 0px 30px;
}

.Layout__Layout--_ISxv{
    font-family: "Open Sans", serif;
    /* background: linear-gradient(to right, #e67e22, #e74c3c); */
    /* background-image: url('../../images/night.jpg'); */

    letter-spacing: 2px;
    display: -webkit-flex;
    display: flex;
    padding: 0;
    margin: 0;
    height: 100vh;
    width: 100vw;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-transition: all 1.5s ease-in-out;
    transition: all 1.5s ease-in-out;

    
}

.Layout__Night--1AlxT{
    background-image: url(/static/media/night.cebf31a0.jpg)
}

.Layout__Day--tjA7X{
    background-image: url(/static/media/day.78ea50eb.jpg);

    
}

.Layout__Layout--_ISxv main{
    color: #fff;

    height: 70vh;
    width: 70vw;
    background: #2c3e50;

    border: 2px solid green;
    border-radius: 20px;
    box-shadow: 10px 10px 10px rgba(0,0,0, .3);
    -webkit-transition: all .5s;
    transition: all .5s;
}
.Layout__Layout--_ISxv main:hover{
    box-shadow: 10px 10px 10px rgba(0,0,0, .8);
    
}

@media (max-width: 600px){
    .Layout__Layout--_ISxv main{
        width: 90vw;
        border: none;
        box-shadow: none;
        height: -webkit-max-content;
        height: -moz-max-content;
        height: max-content;
        
    }
    
}
.Form__Form--1sXHR{
    text-align: center;
}
.Form__Form--1sXHR input{
    background: transparent;
    border: 0;
    width: 40%;
    padding-bottom: 4px;
    color: #fff !important;
    font-weight: lighter;
    letter-spacing: 2px;
    margin-bottom: 30px;
    margin-right: 20px;
    font-size: 20px;
    border-bottom: 1px solid #eee;
}
.Form__Form--1sXHR input:focus{
    outline: none
}
.Form__Form--1sXHR button{
  border: 0;
  padding: 8px 20px;
  margin: 0 2px;
  border-radius: 2px;
  font-weight: lighter;
  letter-spacing: 1px;
  font-size: 15px;
  cursor: pointer;
  background-color: #f16051;
  color: #fff;
  font-weight: 100;
}
.Weather__Weather--1Z6Vl{
    display: grid;
